.App {
  text-align: center;
}
.banner-image{
   padding-bottom:80px;
    text-align:center;
    color:#f8f8f8;
    background: url('data/pic.jpeg') no-repeat center;
    background-size:cover;
    margin:0;
    height:100%;
    width:100%;
    display:flex;
    }
.inner-banner-image{
    padding-top:2%;
  
    width:80%;
    margin:auto;
    justify-content:center;
    }



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}